<template>
  <div class="game">
    <div class="continer">
      <div class="_aviator_">

        <div class="header_">
          <Header :balance="gamer.balance" :isAvtiveChat="activeChat" />
        </div>
        <div class="bets_">
          <Bets :bets="bets" />
        </div>
        <div class="aviator_">
          <Aviator ref="naviator" :startGame="startGame" :betStart="betStart" :kfc="kfc" :betStarts="activeBet" />
        </div>
      </div>


      <div class="chat_" v-if="activeChat">
        <Chat :msgs="msgs" :onlineUsers="onlineUsers"/>
      </div>
    </div>

  </div>
</template>

<script>
import Bets from './Bets.vue'
import Socket from '../js/socket'
import { mapState } from 'vuex';
import Chat from './Chat.vue';
import Header from './Header.vue';
import Aviator from './Aviator';


export default {
  name: 'Game',

  provide() {
    return {
      sendMassege: this.sendMassege,
      bet: this.bet,
      closeChat: () => {this.activeChat = false},
      openChat: () => {this.activeChat = true}

    }
  },

  computed: {
    ...mapState(['gamer'])
  },

  components: {
    Header,
    Bets,
    Aviator,
    Chat
  },

  data() {
    return {
      bets: [],

      betStart: false,
      msgs: [],
      socket: null,
      activeBet: false,
      startGame: false,
      kfc: 1,
      onlineUsers: 0,
      activeChat: true,
    }
  },

  created() {

    this.socket = Socket.connect(this.gamer);

    this.setSocketLisstenets()
  },

  methods: {

    sendMassege(mss) {
      this.socket.emit('new-massege', {from: this.gamer.name, msg: mss, like: 0, color: this.gamer.color})
      console.log('new-message');
      this.activeChat = true
    },

    setSocketLisstenets() {
      this.socket.on('all-messages', (data) => {
      this.msgs = data.msgs
      this.activeChat = true
      console.log('new-message');

      });

      this.socket.on('online-users', ({users}) => {
        this.onlineUsers = users;
      })
      
      this.socket.on('new-massege', (data) => {
        this.msgs.push(data)
        this.activeChat = true
        console.log('new-message')
      });

      this.socket.on('start-bets', () => {
        console.log('startBets');
        this.$refs.naviator.activeBets()
        this.activeBet = true
      })

      this.socket.on('end-bets', () => {
        console.log('endBets');
        this.activeBet = false
      })

      this.socket.on('start-show-game', () => {
        console.log('start-show-game');
        this.startGame = true;

      })

      this.socket.on('end-one-game', () => {
        console.log('end-one-game');
        this.startGame = false;
        this.kfc = 1
      })

      this.socket.on('start-game', () => {
        console.log('start');
      })

      this.socket.on('all-bets', ({bets}) => {
        this.bets = bets;
        console.log(this.bets)
      })

      this.socket.on('show-kalficent', ({kfc}) => {
        this.kfc = kfc;
      })
    },

    bet(betData) {
      this.socket.emit('bet', betData);
    },

    startBetting() {
      
    },
    endBetting() {
      
    } 
  }
}
</script>

<style>

.game {
  display: block;
  width: 100%;
  height: 100%;
}

.continer {
  width: 100%;
  height: 100%;
  display: flex;
}

._aviator_ {
  width: 80%;
  height: 100%;
}

.bets_ {
  width: 30%;
  height: 95%;
  float: left;
}

.aviator_ {
  width: 70%;
  height: 100%;
  float: left;
}

.chat_ {
  width: 20%;
  height: 100%;
}
@media only screen and (max-width: 1120px) {
  .continer {
    position: relative;
  }
  .header_ {
    z-index: 99999999999999;
    position: fixed;
    width: 80%;
  }
  ._aviator_ {
    overflow: auto;
    position: relative;
  }
  .bets_ {
    position: absolute;
    width: 100%;
    top: 100%
  }
  .aviator_ {
    margin-top: 60px;
    width: 100%;
    float: right;
  }
  .chat_ {
    z-index: 99999999999;
  }
}

@media only screen and (max-width: 850px) {
  .continer {
    position: relative;
  }
  .header_ {
    position: fixed;
    width: 100%;
  }
  ._aviator_ {
    width: 100%;
  }
  .bets_ {
    position: absolute;
    width: 100%;
  }
  .chat_ {
    position: fixed;
    width: 100%;
    background-color: #101011;
  }
  .aviator_ {
    margin-top: 60px;
    width: 100%;
    float: right;
  }
}

@media only screen and (max-width: 680px) {
  .continer {
    position: relative;
    
  }
  .header_ {
    position: fixed;
    width: 100%;
  }
  ._aviator_ {
    width: 100%;
  }
  .bets_ {
    position: absolute;
    width: 100%;
    top: 100%
  }
  .chat_ {
    position: fixed;
    width: 100%;
    background-color: #101011;
  }
  .aviator_ {
    margin-top: 60px;
    width: 100%;
    float: right;
  }
}

@media only screen and (max-width: 450px) {

  .bets_ {
    top: 80%
  }
  .aviator_ {
    height: 75%;
  }

}


</style>
