<template>
  <div class="naviator_continer">
    <div class="naviator">

      <div class="kfc" v-if="kfc != '1'" >
          <h1>{{kfc}} X</h1>

      </div>
      <div class="start_bets">
        <div class="loading" v-if="betStarts">
          <h1>Նոր Փուլը Սկսվում Է</h1>
        </div>
      </div>
      <div class="gif" :class="{'passive': !startGame}">
        
      </div>
    </div>
    <div class="_bet">
        <div class="_bet_item">
          <input type="number" v-model="bets[0]">
          <button @click="() => bet_(0)" :disabled="isBet0" >Խազադրույք</button>
        </div>
        <div class="_bet_item">
          <input type="number" v-model="bets[1]">
          <button @click="() => bet_(1)" :disabled="isBet1" >Խազադրույք</button>
        </div>
      </div>
  </div>
</template>

<script>
import { Bet } from '../js/classes'
import { mapState } from 'vuex';


export default {
  name: 'Aviator',

  props: ['startGame', 'betStarts', 'kfc'],

  inject: ['bet'],

  data() {
    return {
      bets: ["", ""],
      isBet0: false,
      isBet1: false,
    }
  },

  
  computed: {
    ...mapState(['gamer'])
  },

  methods: {
    bet_(betId) {
      if(this.bets[betId]) {
        let bet = new Bet(this.gamer.name, this.bets[betId], this.gamer.id);
        console.log(bet);
        this.bet(bet)
        this[`isBet${betId}`] = true;
        this.bets[betId] = ''
      }
    },

    activeBets() {
      this.isBet0 = false;
      this.isBet1 = false;
    }
  }
}
</script>

<style>

.naviator_continer {
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.naviator {
  width: 100%;
  height: 70%;
  border: solid 1px rgb(56, 56, 56);
  border-radius: 20px;
  background-color: #f35035;
  position: relative;
  transition: all .5s;
  overflow: hidden;
}

.gif {
  width: 100%;
  height: 100%;
  background-image: url(../assets/aviator.gif);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  transition: all 2s;
  bottom: 0%;
  left: 0%;
}
.passive  {
  height: 73px;
  width: 200px;
  bottom: -20%;
  left: -100%;
}

._bet {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-top: 5px;
  border-radius: 20px;
  overflow: hidden;
  width: 100%;
}

._bet_item {
  width: 50%;
  background-image: url(../assets/bg.png);
  padding: 1rem;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

._bet_item input {
  -webkit-appearance: none;
}

input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

._bet_item button {
  cursor: pointer;
  background-color: green;
}


._bet_item button:disabled {
  cursor: default;
  background-color: rgb(28, 90, 28);
  color: rgb(102, 102, 102);
}

.kfc {
  width: 100%;
  position: absolute;
  height: 60px;
  z-index: 555;
  top: 20%;
  text-align: center;
  font-size: 30px;
  color: rgb(87, 87, 87);
}

.start_bets {
  position: absolute;
  width: 100%;
  height: auto;
  z-index: 999999;
  text-align: center;
  top: 20%;
}

.loading {

  width: 100%;
  height: 200px;
  background-image: url(../assets/loading.gif);
  background-size: contain; 
  background-repeat: no-repeat;
  background-position: center;
  font-size: 25px;
  color: rgb(158, 161, 163);
}

@media only screen and (max-width: 400px) {
  ._bet_item button {
    padding: 3px 7px;
    font-size: 15px;
  } 

  ._bet_item {
    padding: 1rem .3rem;

  }

  .loading {
    font-size: 13px;
  }

}

</style>
